body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.about__me, .about__the-site {
  background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5));
  border: 3px solid silver;
  border-radius: 1rem;
  padding: 2rem;
  margin-bottom: 5rem;
  align-items: center; }
  .about__me h2, .about__the-site h2 {
    text-align: center; }

.about__me {
  display: grid;
  grid-template-columns: 25rem 1fr;
  grid-template-rows: 6rem 1fr; }
  .about__me h2 {
    grid-column: 1/-1; }
  .about__me--pic1 {
    border-radius: 10%;
    width: 20rem; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media only screen and (max-width: 62.5em) {
    html {
      font-size: 50%; } }

body {
  font-weight: 300;
  line-height: 1.6; }

.content {
  margin: 0 auto;
  padding: 8rem;
  max-width: 180rem;
  min-height: 100vh;
  font-size: 1.6rem;
  background-image: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)), url(/static/media/the-blends-2-1156219.1b9f9603.jpg);
  background-position: center;
  background-repeat: repeat-y; }
  @media only screen and (max-width: 50em) {
    .content {
      padding: 4rem; } }
  @media only screen and (max-width: 25em) {
    .content {
      padding: 1rem; } }

h1 {
  text-align: center;
  font-size: 6rem;
  font-weight: 300;
  margin-bottom: 5rem; }

h2 {
  font-size: 3rem;
  font-weight: 400; }

h4 {
  font-size: 2rem; }

ul {
  margin-left: 2rem; }
  ul li {
    list-style: inside; }

.experience__intro, .experience__place {
  padding: 2rem 10rem 2rem 10rem;
  margin: 0 10rem 0 10rem;
  line-height: 2;
  background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5));
  border: 3px solid silver;
  border-radius: 1rem; }
  @media only screen and (max-width: 50em) {
    .experience__intro, .experience__place {
      padding: 0 1rem 0 1rem;
      margin: 0 1rem 0 1rem; } }

.experience__navbar {
  padding: 2rem;
  margin: 5rem 0 5rem 0;
  border-top: 4px solid lightgrey;
  border-bottom: 4px solid lightgrey;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 2rem;
  align-items: center;
  background-image: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)); }
  @media only screen and (max-width: 50em) {
    .experience__navbar {
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 4rem 0; } }
  .experience__navbar a {
    text-decoration: none;
    color: black;
    font-weight: 600;
    font-size: 2rem;
    color: darkslategrey;
    transition: all 0.5s;
    text-align: center; }
    .experience__navbar a:hover {
      color: black;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      transition: all 0.5s; }

.experience__place {
  margin-top: 5rem; }
  .experience__place--name {
    font-weight: 600; }
  .experience__place--info {
    margin-left: 2rem; }

header,
footer {
  background-color: dodgerblue;
  padding: 2rem 0 2rem 0; }
  header nav,
  footer nav {
    color: white;
    margin: 0 auto;
    width: 60rem;
    display: flex;
    justify-content: space-between; }
    @media only screen and (max-width: 37.5em) {
      header nav,
      footer nav {
        width: 32rem; } }
    header nav a,
    footer nav a {
      color: inherit;
      font-size: 1.6rem;
      text-decoration: none;
      text-transform: uppercase; }

footer .footer-section {
  width: 40%; }
  footer .footer-section img {
    width: 5rem;
    margin: 1rem; }
    @media only screen and (max-width: 37.5em) {
      footer .footer-section img {
        width: 3rem; } }
  footer .footer-section .contact-type {
    font-size: 1.6rem;
    font-weight: 700;
    padding: 0 1rem 0 1rem; }
  footer .footer-section .contact-info {
    font-size: 1.4rem; }
    @media only screen and (max-width: 50em) {
      footer .footer-section .contact-info {
        padding-left: 2rem;
        display: block; } }

.projects {
  margin: 0 auto;
  max-width: 100rem; }
  .projects__section {
    margin: 3rem 0 3rem 0;
    border: 3px solid silver;
    border-radius: 1rem;
    padding: 2rem;
    background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)); }
    .projects__section div {
      margin: 1rem; }
    .projects__section h2 {
      text-align: center; }
    .projects__section a {
      text-decoration: none; }
  .projects__intro {
    margin: 0 auto;
    width: 60%; }
  .projects__diversey--examples {
    display: flex;
    justify-content: space-around; }
  .projects__github--example-list {
    width: 100%;
    display: block; }
  .projects__github--example {
    padding: 1rem; }

